export const getStatusLabel = (
  userStatus: UserStatus,
  labelSuccess,
  labelError
): JSX.Element => {
  const map = {
    0: {
      text: labelError,
      color: 'error'
    },
    1: {
      text: labelSuccess,
      color: 'success'
    }
  };

  const { text, color }: any = map[userStatus];

  return <Label color={color}>{text}</Label>;
};

export const getStatusLabelBlog = (
  userStatus: UserStatus,
  labelSuccess,
  labelError,
  labelPending
): JSX.Element => {
  const map = {
    1: {
      text: labelPending,
      color: 'info'
    },
    2: {
      text: labelSuccess,
      color: 'success'
    },
    3: {
      text: labelError,
      color: 'error'
    }
  };

  const { text, color }: any = map[userStatus];

  return <Label color={color}>{text}</Label>;
};

